import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
  {
    path: 'sales-order',
    loadChildren: () => import('./sales-order/sales-order.module').then(m => m.SalesOrderModule),
  },
  {
    path: 'pick-list',
    loadChildren: () => import('./pick-list/pick-list.module').then(m => m.PickListModule),
  },
  {
    path:'packing',
    loadChildren: () => import('./packing/packing.module').then(m => m.PackingModule)
  },
  {
    path: 'pick-list-new',
    loadChildren: () => import('./pick-list-new/pick-list-new.module').then(m => m.PickListNewModule)
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class OutboundRoutingModule { }
